.cw-footer-v2 {
	font-family: var(--font-proxima);
	display: flex;
	background-color: var(--primaryBlue2);
	padding: 1.75rem 1rem;
}

@media (min-width: 1024px) {
	.cw-footer-v2 {
		padding: 2.5rem 5rem;
	}
}

.cw-footer-v2__content {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0.75rem 0.5rem;
	color: white;
	gap: 1.75rem;
	text-align: center;
}

.cw-footer-v2__logo img {
	max-width: 5.25rem;
}

@media (min-width: 1024px) {
	.cw-footer-v2__content {
		flex-direction: row;
		justify-content: space-between;
		padding: 1.125rem 7.75rem;
		text-align: left;
		align-items: start;
	}
}

.cw-footer-v2__logo {
	flex-shrink: 0;
}

.cw-footer-v2__logo img {
	max-width: 6.875rem;
}

.cw-footer-v2__text {
	display: flex;
	flex-direction: column;
	gap: 1.25rem;
}
